.comment-text {
  text-align: left;
  padding: 2px;
  margin-left: 4px;
}
.comment {
  font-size: 16px;
}
.comment-form  {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  border-top: 1px solid #efefef;
  width: 95%;
  margin:0 auto;
  margin-top: 10px;
}
.comment-form input {
  width: 96%;
  font-size: 14px;
  padding: 10px 0;
  margin: 10px 0;
  border: none;
  color: #999;
  background-color: inherit;
}
button {
  background-color: transparent;
  border: none;
  font-size: 16px;
  font-weight: bold;
}
.user {
  font-size: 14px;
  margin-left: 5px;
  color: black;
  font-weight: bold;
}


